@import '~styles/var.scss';

.container {
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
  // border: 1px solid #000;
  box-shadow: 0 0 6px $grey;
  background: #fff;
  z-index: 3;
  transition: all 0.3s ease-out;
  transform: translate3d(0, 0, 0);

  &.hide {
    transform: translate3d(0, 72px, 0);
  }
}

.innerbox {
  display: flex;
  align-items: center;
  height: 70px;

  &-img {
    >img {
      width: 60px;
      height: 60px;
      display: block;
      border-radius: 50%;
    }

    &.active {
      animation: rotate 15s linear infinite;
    }
  }

  &-content {
    width: 700px;
    margin: 0 30px;
    margin-top: 16px;

    .info {
      display: flex;
      margin-top: 7px;
    }
  }

  &-handler {
    display: flex;
    font-size: 30px;
    cursor: pointer;

    >div {
      &:hover {
        color: $theme-color;
      }
    }
  }
}

.hide-show-btn {
  position: absolute;
  left: 30px;
  top: -24px;
  width: 30px;
  height: 24px;
  background: #fff;
  border: 1px solid $grey;
  cursor: pointer;
  font-size: 18px;
  text-align: center;
  border-bottom: 0;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;

  :hover {
    color: $theme-color;
  }
}

@keyframes rotate {
  from {
    transform: rotateZ(0deg)
  }

  to {
    transform: rotateZ(360deg)
  }
}
