@import '~styles/var.scss';

.article-wrapper {
  display: flex;
}

.sidebar {
  width: $side-width;
  margin-left: $side-margin-left;
}

.article {
  flex: 1;

  &-item {
    border-bottom: 1px solid $grey;
    display: flex;
    cursor: pointer;
    border-radius: $border-radius;
    padding: 15px $padding-normal;

    &:hover {
      box-shadow: 0 4px 12px rgba(0, 0, 0, .15);
    }

    &-content {
      flex: 1;
      margin-right: 15px;

      &-title {
        font-size: $title-fontsize;
        color: $title-color;
        font-weight: 700;
        margin-bottom: 8px;
      }

      &-desc {
        margin-bottom: 8px;
      }

      &-info {
        display: flex;

        >div {
          margin-right: 10px;
        }
      }
    }

    &-img {
      width: 120px;

      img {
        width: 100%;
        height: 80px;
      }
    }
  }
}
