@import '~styles/var.scss';
@import '~styles/mixin.scss';

.wrapper {
  position: relative;
  height: 4px;
  cursor: pointer;

  &-bottomline,
  &-topline {
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    height: 4px;
    border-radius: 2px;
  }

  &-bottomline {
    background: $grey;
  }

  &-topline {
    z-index: 2;
    background: $title-color;
  }

  &-dot {
    height: 16px;
    width: 16px;
    border: 4px solid $theme-color;
    border-radius: 50%;
    position: absolute;
    top: -6px;
    left: 0;
    z-index: 3;
    background: #fff;
    cursor: pointer;
  }
}
