@import '~styles/var.scss';

.artilce-container {
  display: flex;
}

.sidebar {
  width: $side-width;
  margin-left: $side-margin-left;
}

.artilce {
  flex: 1;
  overflow: hidden;

  &-title {
    font-size: 30px;
    color: $title-color;
    font-weight: 700;
    letter-spacing: 2px;
    margin: 20px 0;
    text-align: center;
  }

  &-head {
    display: flex;

    &-avatar {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      overflow: hidden;

      >img {
        width: 100%;
        height: 100%;
      }
    }

    &-content {
      margin-left: 20px;
      align-items: center;

      &-name {
        font-size: 16px;
      }

      &-info {
        display: flex;
        font-size: 14px;
        align-items: center;
        margin-top: 3px;

        >div {
          margin-left: 10px;
        }

        >div:first-child {
          margin-left: 0;
        }
      }
    }

    &-sign {
      display: flex;
      align-items: center;
      margin-left: auto;
    }
  }

  &-content {
    margin-top: 30px;
    margin-bottom: 60px;
  }

  &-footer {
    display: flex;
    justify-content: center;
    margin-bottom: 60px;
  }
}
