@import '~styles/var.scss';

.container {
  margin-top: 10px;
  display: flex;
}

.music {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &-item {
    border: 1px solid $grey;
    display: flex;
    cursor: pointer;
    border-radius: $border-radius;
    padding: 15px $padding-normal;
    width: 48%;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    position: relative;

    &.active {
      border: none;

      &::after {
        content: "";
        display: block;
        left: 1px;
        right: 1px;
        top: 1px;
        bottom: 1px;
        position: absolute;
        border: 1px solid $theme-color;
        border-radius: inherit;
        animation: wave 1s ease-out infinite alternate;
      }
    }

    &-img {
      img {
        display: block;
        width: 40px;
        height: 40px;
      }
    }

    &-name {
      margin-left: 20px;
    }

    &-icon {
      margin-left: auto;
      font-size: 30px;
      margin-right: 30px;
      color: #ccc;
    }
  }
}

.side {
  width: 300px;
  margin-left: 40px;
}

@keyframes wave {

  0% {
    border-color: #393;
    box-shadow: 0 0 5px rgba(0, 255, 0, .2), inset 0 0 5px rgba(0, 255, 0, .1), 0 0px 0 #393;
  }

  100% {
    border-color: #6f6;
    box-shadow: 0 0 20px rgba(0, 255, 0, .6), inset 0 0 10px rgba(0, 255, 0, .4), 0 0px 0 #393;
  }

}
